<template>
  <div>
    <div>mapState</div>
    {{countPlusLocalState}}
  <div>{{countAlias}}</div>
  <myoperator></myoperator>
  </div>
</template>
<script>
//  Vuex.mapState
import { mapState } from "vuex";
// 
import myoperator from './operator';
export default {
  data() {
    return {
      localCount: 12,
    };
  },
  components:{
    myoperator,
  },
  computed: mapState({
    // 
    count: state => state.count,

    //  'count'  `state => state.count`
    countAlias: 'count',

    //  `this` 
    countPlusLocalState (state) {
      return state.count + this.localCount
    }
  })
};
</script>


