<template>
  <div>
    <div></div>
    {{nowdata}}
    <div>
      {{count}}
    </div>
  </div>
</template>
<script>
//  Vuex.mapState
import { mapState } from "vuex";
export default {
  data() {
    return {
      data: 123,
    };
  },
  computed: {
    nowdata() {
      return this.data + 1;
    },
    ...mapState({
      // 
      count: state => state.count
    })
  }
};
</script>



